  /*视频播放暂停*/
  videoStartPause: function videoStartPause(id1) {
    var video = document.getElementById(id1);
    var videoO = document.getElementById("videoCenter");
    var oDiv = document.getElementById("videoBox");
    videoO.src = video.src;
    oDiv.style.display = "block";
    videoO.play();
    videoO.onclick = function () {
      if (videoO.paused) {
        videoO.play();
      } else {
        videoO.pause();
      }
    };
  },

